import React, {Component} from 'react';
import {NavLink} from 'react-router-dom';
import {Nav, NavItem} from 'reactstrap';
import PropTypes from 'prop-types';

import {AppNavbarBrand, AppSidebarToggler} from '@coreui/react';
import logo from '../../assets/img/brand/logo.png'
import logoIcone from '../../assets/img/brand/ico.png'

const propTypes = {
  children: PropTypes.node,
};

const defaultProps = {};

class DefaultHeader extends Component {
  render() {

    // eslint-disable-next-line
    const {children, ...attributes} = this.props;

    return (
      <React.Fragment>
        <AppSidebarToggler className="d-lg-none" display="md" mobile/>
        <AppNavbarBrand
          full={{src: logo, width: 151, height: 20, alt: 'Vou de Van Logo'}}
          minimized={{src: logoIcone, width: 30, height: 30, alt: 'Vou de Van Logo'}}
        />
        <AppSidebarToggler className="d-md-down-none" display="lg"/>


        <Nav className="ml-auto" navbar>

          <NavItem className="d-md-down-none">
            <NavLink to="#" className="nav-link" onClick={this.props.onLogout}>
              <i className="icon-logout"/> Sair
            </NavLink>
          </NavItem>
        </Nav>


      </React.Fragment>
    );
  }
}

DefaultHeader.propTypes = propTypes;
DefaultHeader.defaultProps = defaultProps;

export default DefaultHeader;
